
import Vue from "vue";
import ProjectDetailsOverlay from "@/components/ProjectDetailsOverlay.vue";
import ProjectData from "@/data/ProjectData.ts";

export default Vue.extend({
  name: "ProjectsList",
  components: {
    ProjectDetailsOverlay,
  },
  props: {
    projects: Array
  },
  data: function () {
    return {
      showPopup: false,
      popupTitle: "",
      popupColor: "",
      popupTextColor: "",
      popupContent: "",
      hoveredProject: "",
      popupBackgroundImage: "",
      popupMainMargin: 0
    };
  },
  methods: {
    showDetails: function (item: ProjectData) {
      this.popupTitle = item.name;
      this.popupColor = item.accentColor;
      this.popupTextColor = item.textColor;
      this.popupContent = item.htmlDescription;
      this.popupBackgroundImage = item.backgroundImage;
      this.popupMainMargin = item.mainMargin;
      this.showPopup = true;
      window.scrollTo(0, 0);
    },
    hoverProject(id: string, isHover: boolean) {
      this.hoveredProject = isHover ? id : '';
    },
    getImageUrl(project: ProjectData) {
      if (!project.gifCover || project.gifCover.trim() === '') {
        return project.iconUrl;
      }
      return this.hoveredProject === project.id ? project.gifCover : project.iconUrl;
    }
  },
});
