export default class ProjectData {
    id: string;
    name: string;
    year: number;
    htmlDescription: string;
    iconUrl: string; // used as thumnail
    gifCover: string; 
    isWide: boolean; // thumbnail will take 2 cols in the grid view
    isHigh: boolean; // thumbnail will take 2 rows in the grid view
    accentColor: string; // color of title bar 
    textColor: string; // color of title bar
    backgroundImage: string;
    mainMargin:  number;
    isFeatured: boolean;

    constructor(id: string, name: string, year: number, iconUrl: string, gifCover: string, html: string, accentColor = "#000000", 
    textColor = "#000000", isHigh = false, isWide = false, backgroundImage ="", mainMargin = 0, isFeatured = false){
        this.id = id;
        this.name = name;
        this.year = year;
        this.htmlDescription = html;
        this.iconUrl = iconUrl;
        this.gifCover = gifCover;
        this.isHigh = isHigh;
        this.isWide = isWide;
        this.accentColor = accentColor;
        this.textColor = textColor;
        this.backgroundImage = backgroundImage;
        this.mainMargin = mainMargin;
        this.isFeatured = isFeatured;
    }
}
